<template>
  <iframe :src="url" frameborder="0" width="100vw" height="100vh"></iframe>
</template>

<script>
import { ref, reactive, watch } from 'vue';
import { Notify } from 'vant';
import { useRoute, useRouter } from 'vue-router'

export default {
  setup(props) {
    // 路由
    const route = useRoute()
    const router = useRouter()

    // const url = ref('http://localwar.xidayun.com/')
    const url = ref('https://developers.arcgis.com/javascript/latest/sample-code/layers-imagery-afrenderer-blending/live/')

    if (route.params.url) url.value = route.params.url
    return {
      url
    };
  },
};
</script>
<style lang="less">
iframe {
  height: 100vh;
  width: 100vw;
}
</style>